import React, { Component } from "react";
import Aux from "../Aux";
import Navbar from '../Component/CommonComponents/Navbar/Navbar';
import ShopHeader from "../Component/Shop/ShopHeader/ShopHeader";
import PokemonRenderer from "../Component/Shop/PokemonRenderer/PokemonRenderer";
import PageSwitch from "../Component/Shop/PageSwitch/PageSwitch";
import Search from "../Component/Shop/Search/Search";
import ShowCart from "../Component/Shop/ShowCart/ShowCart";
import Cart from "../Component/Shop/Cart/Cart";
import Footer from '../Component/CommonComponents/Footer/Footer';
import ScrollButton from '../Component/CommonComponents/ScrollButton/ScrollButton';

class PokemonsShop extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      eachPokemon: [],
      currentPage: 1,
      filter: "",
      totalPrice: 0,
      pokemonsInCart: [],
      cartOn: false,
      enableCart: false,
      shopOn: true,
    };
    this.inputRef = React.createRef();
  }

  fetchURLs = async () => {
    try {
      const newArr = [];
      for (let i = 1; i < 62; i++) {
        const response = await fetch(`https://pokeapi.co/api/v2/pokemon/${i}/`);
        const data = await response.json();
        newArr.push(data);
      }
      this.setState({ eachPokemon: newArr });
    } catch (err) {
      this.setState({ error: true });
    }
  };

  componentDidMount = async () => {
    this.fetchURLs();
  };

  switchPageHandler = (event) => {
    this.setState({ currentPage: +event.target.textContent });
  };

  filterSearch = () => {
    this.setState({ filter: this.inputRef.current.value });
  };

  addItemToCartHandler = (price, name, id, image) => {
    const pokemonPrice = price / 10;
    const oldPrice = { ...this.state }.totalPrice;
    const newPrice = oldPrice + pokemonPrice;
    const newCart = [...this.state.pokemonsInCart];
    newCart.push({ name: name, price: pokemonPrice, key: id, image: image });
    this.setState(
      { pokemonsInCart: newCart, totalPrice: newPrice, enableCart: true },
      () => {
      }
    );
  };

  showCartBeforeCheckout = (boolean) => {
    this.setState({ cartOn: boolean});
    this.setState({shopOn: !boolean})
  };

  removeItemFromCart = (key) => {
    const newItemsInCart = [...this.state.pokemonsInCart];
    const filteredArray = newItemsInCart.filter(item => {
      return item.key !== key;
    });
    let mapPrice = 0;
    const newPrice = filteredArray.map(pokemon => {
       mapPrice += pokemon.price;
    });
    this.setState({pokemonsInCart: filteredArray, totalPrice: mapPrice});
  }

  saveToHistory = () => {
    this.props.history.push('/');
  }

  render() {
    return (
      <Aux>
        <ScrollButton /> 
        <Navbar active={'Shop'} makeActive={this.saveToHistory} />
  
        <ShopHeader
          shouldUpdate={this.state.eachPokemon}
          eachPokemon={this.state.eachPokemon}
          error={this.state.error}
        />
        <ShowCart
          shouldUpdate={this.state.cartOn}
          priceSumHandler={(b) => this.showCartBeforeCheckout(true)}
          disableButton={!this.state.enableCart === true}
        ></ShowCart>
        {this.state.filter === "" && this.state.shopOn ? (
          <PageSwitch
            currentPage={this.state.currentPage}
            switchPageHandler={(e) => this.switchPageHandler(e)}
          />
        ) : null}
        {this.state.eachPokemon.length > 0 ? (
          <Search
            searchInput={this.state.filter}
            aRef={this.inputRef}
            onChange={this.filterSearch}
          />
        ) : null}
        {this.state.shopOn ? 
        <PokemonRenderer
          currentPage={this.state.currentPage}
          filter={this.state.filter}
          eachPokemon={this.state.eachPokemon}
          addItemToCartHandler={this.addItemToCartHandler}
        /> : null }
              {this.state.cartOn && this.state.shopOn === false ?  (
          <Cart
            header={"Total price:"}
            price={this.state.totalPrice}
            itemsInCart={this.state.pokemonsInCart}
            removeItemFromCart={(key) => this.removeItemFromCart.bind(this, key)}
            cartRemove={() => this.showCartBeforeCheckout(false)}
          />
        ) : null}
        {this.state.eachPokemon.length > 1 && this.state.filter === "" && this.state.shopOn ?(
          <PageSwitch
            currentPage={this.state.currentPage}
            switchPageHandler={(e) => this.switchPageHandler(e)}
          />
        ) : null}
        <Footer active={'Shop'} />
      </Aux>
    );
  }
}
export default PokemonsShop;
