import React, { useEffect } from "react";
import classes from "./Cart.module.css";
import Aux from '../../../Aux';

const Backdrop = (props) => {
  useEffect(() => {

  },[props.itemsInCart])
  return (
    <Aux>
      <div className={classes.cart__container}>
        <h3 style={{marginBottom: 10, textAlign: "center"}}>Items In Cart:</h3>
        <ul className={classes.cartItemsUl}>
        {
        props.itemsInCart.length > 0 ?  props.itemsInCart.map(((pokemon, index) => (
          <li key={pokemon.name}>
            <div className={classes.listItems}>
            <img src={pokemon.image} alt={pokemon.name}></img>
            <span style={{textTransform: 'capitalize'}}><strong>Name:</strong> {pokemon.name}</span>
            <p><strong>Price:</strong> ${pokemon.price}</p>
            <button onClick={props.removeItemFromCart(pokemon.key)} >Delete</button>
            <hr />
            </div>
          </li>
        ))) : <div className={classes.cartIsEmptyWrapper}><p className={classes.cartIsEmpty}>Cart is empty. Click the "Continue Shopping" button to add Pokemons</p></div> 
      }
        </ul>
        </div>
        <div className={classes.container__price}>
        <h3>{props.header}</h3>
        <p className={classes.cart__containerPrice}>${props.price}</p>
        </div>
        <div className={classes.container__button}>
        <button className={classes.cart__containerButton}>Checkout <span style={{fontSize: 11}}>(No backend yet)</span></button>
        <button className={classes.cart__containerButton} onClick={props.cartRemove}>Continue Shopping</button>
      </div>
    </Aux>
  );
};


export default Backdrop;